body{
  background-color: #5380AA;
}

h1 {
  color: aliceblue;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 90px;
  margin: auto;
  width: 50%;
  padding: 10px;
  height: 90px;
  text-align: center;
  vertical-align: middle;
  padding-top: 50px;

}

.courseItem {
  background-color: #F21E1D;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 35px;
  margin: auto;
  margin-top: 20px;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  box-shadow: inset -8px -7px 4px 0px rgba(0,0,0,0.3);
  display: flex;
  align-items: center; 
  justify-content: center;
}

.deleteButton {
  position: relative;
  top: 35px;
  width: 65px;
  height: 40px;
  background-color: #F21E1D;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  border: 1px solid white;
  border-radius: 10%;
  display: flex;
  align-items: center; 
  justify-content: center;
  margin: auto;
}

h2:hover {
  background-color: navy;
  cursor: pointer;
}

h2:active {
  background-color: #F21E1D;
}

form {
  position: relative;
  top:50px;
  margin:auto;
  text-align: center;
  border: 4px solid white;
  margin-right: 100px;
}

.submitbutton {
  position: relative;
  margin:auto;
  justify-content: center;
  display: flex;
  align-items: center;
  background-color: #F21E1D;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  border: 1px solid #F21E1D;
}

.courseInput {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  padding-right:5px;
}

h3 {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 27px;
}

p {
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 15px;
}

.btn {
  text-align: center;
}

button {
  position: relative;
  top: 110px;
  width: 150px;
  height: 50px;
  margin-bottom: 20px;
  background-color: #F21E1D;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  border: 1px solid #F21E1D;
}

button:hover {
  background-color: navy;
  cursor: pointer;
  border: 1px solid navy;
}

button:active {
  background-color: #F21E1D;
  border: 1px solid navy;
}

ul {
  padding-left:0;
  list-style-type: none;
}

.row {
  display: flex;
}

.column {
  flex: 50%;
}